<template>
  <div>
    <v-card
      class="mx-auto"
      max-width="700"
    >
      <v-sheet
        class="noticias__vsheet"
        @click="$router.push({ path: `/noticias-psicologia/noticia/${noticia.Id}` })"
      >
        <v-img
          :src="
            `https://res.cloudinary.com/dkod5pdlc/image/upload/v${ noticia.VersionImagen }/terapiacl/blog/${noticia.Imagen}`
          "
          max-height="300px"
          style="display: flex; color: white; align-items: flex-end; padding: 20px;"
        >
          <v-btn color="warning">
            {{ noticia.Fecha.split('T')[0] }}
          </v-btn>
        </v-img>
        <v-sheet
          class="px-5 pb-5"
          style="background: transparent !important;"
        >
          <v-card-title class="noticias-title mb-5 pt-5">
            <h1>{{ noticia.Titulo }}</h1>
          </v-card-title>

          <v-card-text
            class="noticias-text"
            v-html="
              noticia.Texto.length > 200
                ? noticia.Texto.substring(0, 300) + '...'
                : noticia.Texto
            "
          />
          <v-card-text>
            <router-link
              class="leer-mas"
              :to="`/noticias-psicologia/noticia/${noticia.Id}`"
            >
              Leer más
            </router-link>
          </v-card-text>
        </v-sheet>
      </v-sheet>
    </v-card>
  </div>
</template>

<script>
  export default {
    name: 'CardNoticias',
    props: {
      noticia: {
        Id: {
          type: Number,
        },
        Titulo: {
          typeof: Text,
        },
        Texto: {
          typeof: Text,
        },
        Imagen: {
          typeof: Text,
        },
        Fecha: {
          typeof: Text,
        },
        type: Object,
        default: null,
      },
    },
  }
</script>
<style scoped>
.leer-mas {
  text-decoration: none;
  font-weight: bold;
  color: #3a9cba !important;
}
.noticias-text {
  color: #7e868c;
}
.noticias-title {
  color: #595959;
}

.noticias__vsheet {
  background: #ffff;
}

.noticias__vsheet:hover {
  background: #e4e8eb !important;
  cursor: pointer;
}
</style>
